import React, { Component } from 'react';
import './StateDetails.css';
import $ from 'jquery';
import Card from '@material-ui/core/Card';

class StateDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dynamicClassname:'extraCls',
      sliderLog:''
    }
  }
  componentDidMount(){
    this.autoSlideCards();
  }
  autoSlideCards(){
    this.state.sliderLog = setInterval(()=>{
      if(!document.querySelector("#root > div > div > div.MuiGrid-root.makeStyles-ThirdGridRow-242.MuiGrid-container > div > div > button.flickity-button.flickity-prev-next-button.next").disabled){
        let slideClick = document.querySelector("#root > div > div > div.MuiGrid-root.makeStyles-ThirdGridRow-242.MuiGrid-container > div > div > button.flickity-button.flickity-prev-next-button.next");
        slideClick.click();
      }else{
        let gotostart = document.querySelector("#root > div > div > div.MuiGrid-root.makeStyles-ThirdGridRow-242.MuiGrid-container > div > div > ol > li:nth-child(1)");
        gotostart.click()
      }
    },2000)
  }
  handleflipCard(extra) {
    clearInterval(this.state.sliderLog)

    setInterval(()=>{
      this.autoSlideCards()
    },20000)

    let cardTransitionTime = 500;

    let $card = $('.'+extra)
    let switching = false

    if (switching) {
      return false
    }
    switching = true

    $card.toggleClass('is-switched')
    window.setTimeout(function () {
      $card.children().children().toggleClass('is-active')
      switching = false
    }, cardTransitionTime / 2)
  }
  
  render() {
    const cardStlying = {
      boxShadow: '0 10px 20px rgba(10,10,10,0.40)'
      }
    return (
      <React.Fragment>
        <div className="gallery js-flickity"
          data-flickity-options='{ "freeScroll": true }'>
          <div title="Click For More Detalis" className={"gallery-cell js-card "+ this.state.dynamicClassname} onClick={this.handleflipCard.bind(this,this.state.dynamicClassname)}>
          <Card variant="outlined" style={cardStlying}>
            <div className="card__wrapper">
              <div className="card__side is-active">
                <h1>FRONT</h1>
                <h1>FRONT</h1>
                <h1>FRONT</h1>
              </div>
              <div className="card__side card__side--back">
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
              </div>
            </div>
            </Card>
          </div>
          <div className="gallery-cell js-card" onClick={this.handleflipCard.bind(this)}>
          <Card variant="outlined" style={cardStlying}>
          <div className="card__wrapper">
              <div className="card__side is-active">
                <h1>FRONT</h1>
                <h1>FRONT</h1>
                <h1>FRONT</h1>
              </div>
              <div className="card__side card__side--back">
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
              </div>
            </div>
            </Card>
          </div>
          <div className="gallery-cell js-card" onClick={this.handleflipCard.bind(this)}>
          <Card variant="outlined" style={cardStlying}>
          <div className="card__wrapper">
              <div className="card__side is-active">
                <h1>FRONT</h1>
                <h1>FRONT</h1>
                <h1>FRONT</h1>
              </div>
              <div className="card__side card__side--back">
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
              </div>
            </div>
            </Card>
          </div>
          <div className="gallery-cell js-card" onClick={this.handleflipCard.bind(this)}>
          <Card variant="outlined" style={cardStlying}>
          <div className="card__wrapper">
              <div className="card__side is-active">
                <h1>FRONT</h1>
                <h1>FRONT</h1>
                <h1>FRONT</h1>
              </div>
              <div className="card__side card__side--back">
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
              </div>
            </div>
            </Card>
          </div>
          <div className="gallery-cell js-card" onClick={this.handleflipCard.bind(this)}>
          <Card variant="outlined" style={cardStlying}>
          <div className="card__wrapper">
              <div className="card__side is-active">
                <h1>FRONT</h1>
                <h1>FRONT</h1>
                <h1>FRONT</h1>
              </div>
              <div className="card__side card__side--back">
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
                <h2>BACK</h2>
              </div>
            </div>
            </Card>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default StateDetails;